import Vue from 'vue'
import { Component, Prop, Watch } from 'vue-property-decorator'
import template from './list.vue'

import NavHeader from '../unit/navHeader'  // 
import NavFooter from '../unit/navFooter'  // 
import ListView from './unit/listView'      // 

@Component({
    name: 'list',
    mixins: [template],
    components: {
        NavHeader,
        NavFooter,
        ListView
    }
})
export default class List extends Vue {
    title = 'demo1'
    // 
    footerItem = [
        '1',
        '2',
        '3'
    ]

    created() {
        console.log('')
        // console.log(this.title)
    }
}



